import React, { useEffect, useState } from 'react';
import p from './ProfileInfo.module.scss';
import loader from '../../../assets/images/loader.gif'
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHook from './ProfileStatusWithHook';
import profPhoto from './prof.jpg';
import { ProfileDataFormmm } from './ProfileDataForm';
import { ProfileType, ProfileContact } from '../../common/types/types';


type ProfileInfoType = {
    profile: ProfileType,
    status: string,
    isOwner: boolean,
    setStatus: (status: string) => void,
    saveProfile: (FormData: ProfileType) => void
}

const ProfileInfo: React.FC<ProfileInfoType> = ({ profile, isOwner, setStatus, status, saveProfile }) => {
    let [editMode, setEditMode] = useState(false)
    if (!profile) { return <div><img src={loader} alt="loader" /></div> }

    const onSubmitt = (FormData: ProfileType) => {
        saveProfile(FormData)
        setEditMode(false)
    }

    return (
        <div>
            <img className={p.proffPhoto} src={profile.photos.large || profPhoto} />
            {editMode
                ? <ProfileDataFormmm initialValues={profile} profile={profile} onSubmit={onSubmitt} />
                : <ProfileData goToEditMode={() => { setEditMode(true) }} profile={profile} isOwner={isOwner} />}

            <ProfileStatusWithHook status={status} setStatus={setStatus} />
        </div>
    )
}

type ProfileDataType = {
    profile: ProfileType,
    isOwner: boolean,
    goToEditMode: () => void,
}

const ProfileData: React.FC<ProfileDataType> = ({ profile, isOwner, goToEditMode }) => {
  
    return <div className={p.profFomr}>
        {isOwner && <div><button className={p.editBtn} onClick={goToEditMode}>edit</button></div>}
        <div className={p.OnefieldProf}>
            <b>Full Name</b>  {profile.fullName}
        </div>
        <div className={p.OnefieldProf}>
            <b>lookingForAJob</b>  {profile.lookingForAJob ? "yes" : "no"}
        </div>
        <div className={p.OnefieldProf}>
            <b>My proffesional skills</b>  {profile.lookingForAJobDescription}
        </div>
        <div className={p.OnefieldProf}>
            <b>About me</b>  {profile.aboutMe}
        </div>
        <div >
            <b>Contacts</b> : {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key as keyof ProfileContact]} />
            }

            )}
        </div>

    </div>
}

type ContactType = {
    contactTitle: string,
    contactValue: string
}

export const Contact: React.FC<ContactType> = ({ contactTitle, contactValue }) => {
    return <div><b>{contactTitle}</b> : {contactValue}</div>
}

export default ProfileInfo