

import { Field, reduxForm,InjectedFormProps } from "redux-form";
import { FormForControls } from "../../common/FormsControl/FormsControl";
import { Contact } from "./ProfileInfo";
import {ProfileType}from "../../common/types/types"
import p from './ProfileInfo.module.scss';
import { useEffect } from "react";
const FullName = FormForControls("textarea");


type ProfDataType = {
    profile:ProfileType
}

const ProfData:React.FC<InjectedFormProps<ProfileType,ProfDataType>&ProfDataType> = ({handleSubmit,profile,error}) => {
   

    return <form onSubmit={handleSubmit}>
            <div><button  className={p.editBtn}>save</button></div>
            {error && <div></div>}
            <div>
                <b>Full Name</b> :
                <Field component={"input"} name="fullName"/>
            </div>
            <div>
                <b>lookingForAJob</b> :
                <Field component={"input"} type="checkbox" name="lookingForAJob"/>
            </div>
            <div>
                <b>My proffesional skills</b> : <Field component={"textarea"} name="lookingForAJobDescription" placeholder="my professional skills"/>
            </div>
            <div>
                <b>About me</b> : <Field component={"textarea"} name="aboutMe"/>
            </div>
            <div>{Object.keys(profile.contacts).map(key=>{
           return  <div key={key}> <b> {key}  <Field component={"input"} name={"contacts."+key}/></b></div>
            })}</div>
            
        </form>
}

export const ProfileDataFormmm = reduxForm<ProfileType,ProfDataType>({ form: "formddd" })(ProfData)